import React, { useState } from "react";
import ReactDOM from "react-dom";
// import Content from "./components/course";
// import Book from "./components/Book";

const Book = () => {
  const Persons = [
    {
      name: "HTML is easy"
    },
    {
      name: "Browser can execute only Javascript"
    },
    {
      name: "GET and POST are the most important methods of HTTP protocol"
    }
  ];

  const addNumber = event => {
    event.preventDefault();
    const personObject = {
      name: newName
    };

    setPersons(persons.concat(personObject));
    setNewName("");
  };

  const [persons, setPersons] = useState(Persons);
  const [newName, setNewName] = useState("");

  const rows = () =>
    Persons.map(person => <li key={person.name}> {person.name} </li>);

  const handlePersonChange = e => {
    console.log(e.target.value);
    setNewName(e.target.value);
  };

  return (
    <div>
      <h2>Phonebook</h2>
      <form>
        <div>
          name: <input onChange={handlePersonChange} value={newName} />
        </div>
        <div>
          <button type="submit" onSubmit={addNumber}>
            add
          </button>
        </div>
      </form>
      <h2>Numbers</h2>
      <div>
        <ul>{rows()}</ul>
      </div>
    </div>
  );
};

const App = () => {
  return (
    <React.Fragment>
      {/* <h2>Web development curriculum</h2> */}
      {/* <Content /> */}
      <Book />
    </React.Fragment>
  );
};

ReactDOM.render(<App />, document.getElementById("root"));
